.app-utility {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: var(--box-padding);
  grid-row-gap: var(--box-padding);
  -webkit-column-gap: var(--box-padding);
          column-gap: var(--box-padding);
  grid-column-gap: var(--box-padding);
  padding: var(--box-padding);
}

.utility-button > button {
  width: 100%;
  height: 100%;
  font-size: 30px;
  font-weight: 400;
  background-color: var(--highlight-colour);
  color: var(--highlight-font-colour);
  outline: none;
  border: none;
}

.utility-button > button:hover {
  background-color: var(--light-highlight-colour);
}

.utility-button > button:active {
  background-color: var(--dark-highlight-colour);
}

@media only screen and (max-width: 430px) {
  .app-utility {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}


