  width: 100%;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}

.app-content {
  padding: var(--box-padding);
  padding-bottom: 0px;
}

.app-content > div {
  margin-bottom: var(--box-padding);
}

.app-graphs {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.app-graph {
  height: 100%;
}

.app-controls {
  width: 100%;
  display: grid;
  grid-template-columns: 3fr 2fr;
}

.app-utility {
  background-color: var(--primary-colour);
  height: 100%;
}

.app-util-box {
  height: 100%;
}

.app-util-box:first-child { padding-right: calc(var(--box-padding) / 2.0); }
.app-util-box:last-child { padding-left: calc(var(--box-padding) / 2.0);}
.app-graph:first-child { padding-right: calc(var(--box-padding) / 2.0); }
.app-graph:last-child { padding-left: calc(var(--box-padding) / 2.0);}

.sleep-time-box {
  display: none;
}

@media only screen and (max-width: 700px) {
  .app-graphs {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: var(--box-padding);
    grid-row-gap: var(--box-padding);
  }
  .app-graph {
    padding-right: 0px !important;
    padding-left: 0px !important;
  }
  .app-controls {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: var(--box-padding);
    grid-row-gap: var(--box-padding);
  }
  .app-util-box {
    padding-right: 0px !important;
    padding-left: 0px !important;
  }
  #suggested-sleep-time-header {
    display: none;
  }
  .sleep-time-box {
    display: block;
  }
}

@media only screen and (min-width: 1600px) {
  .App {
    width: 1600px;
    margin: auto;
    margin-top: 30px;
    /* box-shadow: 5px 5px 5px 2px #17171752; */
  }
}


