.record-form {
  height: 100%;
  background-color: var(--primary-colour);
  padding: 20px;
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 0px;
}

.record-form-title {
  margin-left: -5px;
  margin-bottom: 20px;
  color: white;
}

.record-content {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.record-labels {
  display: grid;
  grid-template-rows: 60px 60px;
}

.record-fields {
  display: grid;
  grid-template-rows: 60px 60px;
}

.record-field {
  display: block;
  height: 30px;
  width: 100%;
}

.record-label {
  color: white;
  width: 100%;
}

.record-field > .record-input {
  width: 100%;
  font-size: 20px;
  padding: 5px;
  background-color: var(--secondary-colour);
  border: none;
  color: var(--dark-secondary-colour);
}

.record-field > .record-input:focus {
  outline: 3px solid white;
}

.record-field > .input-range {
  width: 100%;
  margin-top: 10px;
  -ms-touch-action: pan-x;
      touch-action: pan-x;
}

.input-range__label--min, .input-range__label--max {
  display: none;
}

.record-submit-button {
  width: 100%;
  height: 60px;
  margin-top: 16px;
  margin-bottom: 11px;
  font-size: 30px;
  font-weight: 400;
  background-color: var(--highlight-colour);
  color: var(--highlight-font-colour);
  outline: none;
  border: none;
}

.record-submit-button:hover {
  background-color: var(--light-highlight-colour);
}

.record-submit-button:active {
  background-color: var(--dark-highlight-colour);
}

.record-submit-button:disabled {
  background-color: var(--highlight-colour);
  text-decoration: line-through;
}

.record-warning {
  display: inline-block;
  width: 100%;
  text-align: center;
  font-size: 15px;
  color: orange;
  margin: 0px;
}

@media only screen and (max-width: 700px) {
  .record-submit-button {
    margin-bottom: 15px;
  }
}

@media only screen and (max-width: 470px) {
  .label-xtra {
    display: none;
  }
}


