.sleep-records {
  background-color: var(--primary-colour);
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(auto-fill, 40px);
  row-gap: 10px;
  grid-row-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  grid-column-gap: 10px;
  padding: 10px;
}

.sleep-record {
  width: 100%;
  height: 40px;
  line-height: 40px;
  vertical-align: middle;
  padding-left: 10px;
  background-color: var(--secondary-colour);
  color: var(--record-font-colour);
}

.record-label {
  line-height: 40px;
  vertical-align: middle;
}

.sleep-record > button {
  float: right;
  height: 100%;
  width: 40px;
  background-color: var(--highlight-colour);
  color: var(--highlight-font-colour);
  outline: none;
  border: none;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.sleep-record > button:last-child {
  border-right: solid 2px var(--light-highlight-colour);
}

.sleep-record > button:hover {
  background-color: var(--light-highlight-colour);
}

.sleep-record > button:active {
  background-color: var(--dark-highlight-colour);
}

.edit-input {
  width: 30px;
  height: 20px;
  margin-left: 0px;
  margin-right: 0px;
  margin-bottom: 2px;
  line-height: 40px;
  vertical-align: middle;
  border-radius: 3px;
  text-align: center;
  outline: none;
  border: none;
  background-color: var(--light-highlight-colour);
  color: var(--secondary-colour);
}

.edit-input:first-child {
  margin-left: 3px;
}

.no-sleep-records {
  color: white;
}

@media only screen and (max-width: 950px) {
  .sleep-records {
    grid-template-columns: 1fr;
  }
}


